@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.header {
    background-color: #ffffff;
    min-height: 20vh;
    display: flex; 
    flex-direction: column;
    align-items: center;
    font-family: 'Lato';
    justify-content: center;
    font-size: 72px;
    color: rgb(154, 117, 255);
}
.footer {
    background-color: #ffffff;
    min-height: 10vh;
    display: flex; 
    flex-direction: column; 
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    font-family: 'Lato';
    color: rgb(154, 117, 255);
    position: fixed;
    bottom: 0;
    left:0;
    width: 100%;
}
.airportinfo {
    width: 80%;
    margin: 0 auto; 
    padding: 40px; 
    border: 0px dotted black; 
    height: 80vh; 
    overflow-y: auto;
    display: flex; 
    flex-direction: column;
    align-items: center;
}

form {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Lato';
}

.search {
    background-color: #aa7dff;
    padding: 20px 20px;
    height: 10px;
    color: white; 
    border-radius: 15px;
    display: block;
    line-height: 0px;
    font-size: 20px;
    font-family: 'Lato';
}

label {
    font-size: 20px;
}


#queryInput {
    box-shadow: 0 0 15px 4px rgba(0,0,0,0.06);
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
}
.places {
    padding-top: 20px;
}

table {
    border-collapse: collapse;
}  

td, th {
    border: 1px solid #999;
    padding: 0.5rem;
    text-align: left;
}
  
  
